<template>
  <div id="line" style="height: 300px;">
  </div>
</template>

<script>
import  * as echarts from 'echarts'
export default {
  name: "echarts-line",
  data() {
    return {}
  },
  mounted() {
    this.echartsInit()
  },
  methods: {
    echartsInit() {
      //2. 基于准备好的dom，初始化echarts实例
      echarts.init(document.getElementById('line')).setOption({
        //3. 指定图表的配置项和数据
        title: {
          text: '一天内空气质量变化',
          textStyle: {
            //文字颜色
            color: '#fff',
          },
            top:'10px',
        },
        //设置图表大小
        grid:{
          x:50,
          y:90,
          x2:50,
          y2:60,
          borderWidth:1
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          bottom:'10px',
          textStyle: {
            //文字颜色
            color: '#fff',
          },
        },
        toolbox: {
          show: true,
          top: '30px',
          iconStyle:{
            borderColor:'#blue',
            backgroundColor:'fff',
          color:'#fff',
    },
          feature: {
            dataZoom: {
              yAxisIndex: ''
            },
            dataView: { readOnly: false },
            magicType: { type: ['line', 'bar'] },
            restore: {},
            saveAsImage: {}
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: ['0：00', '4：00', '8：00', '12：00', '16：00', '20：00']
        },
        yAxis: {
          type: 'value',
          axisLabel: {
            formatter: '{value} '
          }
        },
        series: [
          {
            name: '滁州市空气质量',
            type: 'line',
            radius:'30%',
            center:['30%','30%'],
            areaStyle: {
              normal: {
                color: 'fff' //改变区域颜色
              }
            },

            data: [78, 86, 140, 120, 100, 60],
            markPoint: {
              data: [{ name: '周最低', value: -2, xAxis: 1, yAxis: -1.5 }]
            },
            markLine: {
              data: [
                { type: 'average', name: 'Avg' },
                [
                  {
                    symbol: 'none',
                    x: '90%',
                    yAxis: 'max'
                  },
                  {
                    symbol: 'circle',
                    label: {
                      position: 'start',
                      formatter: 'Max'
                    },
                    type: 'max',
                    name: '最高点'
                  }
                ]
              ]
            }
          }
        ]
      })
    }
  }
}
</script>

