<template>
  <div id="bar" style="height: 350px;">
  </div>
</template>

<script>
import  * as echarts from 'echarts'
export default {
  name: "echartsbar",
  data() {
    return {}
  },
  mounted() {
    this.echartsInit()
  },
  methods: {
    echartsInit() {
      //2. 基于准备好的dom，初始化echarts实例
      echarts.init(document.getElementById('bar')).setOption({
        //3. 指定图表的配置项和数据
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            // Use axis to trigger tooltip
            type: 'shadow' // 'shadow' as default; can also be 'line' or 'shadow'
          }
        },
        legend: {},
        grid: {
          left: '0%',
          right: '0%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'value'
        },
        yAxis: {
          type: 'category',
          data: ['滁州市', '合肥市', '芜湖市', '宿州市', '铜陵市', '安庆市', '马鞍山市']
        },
        series: [
          {
            name: 'CO',
            type: 'bar',
            stack: 'total',
            color:'#63b2ee ',
            label: {
              show: true
            },
            emphasis: {
              focus: 'series'
            },
            data: [320, 302, 301, 334, 390, 330, 320]
          },
          {
            name: 'NO2',
            type: 'bar',
            stack: 'total',
            color:'#76da91 ',
