          },
          {
            name: 'PM2.5',
            type: 'bar',
            stack: 'total',
            color:'#7cd6cf ',
            label: {
              show: true
            },
            emphasis: {
              focus: 'series'
            },
            data: [100, 130, 89, 134, 90, 230, 210]
          },
          {
            name: 'PM10',
            type: 'bar',
            stack: 'total',
            color:'#9192ab ',
            label: {
              show: true
            },
            emphasis: {
              focus: 'series'
            },
            data: [220, 182, 191, 234, 290, 330, 310]
          },
          {
            name: 'SO2',
            type: 'bar',
            stack: 'total',
            color:'#7898e1 ',
            label: {
              show: true
            },
            emphasis: {
              focus: 'series'
            },
            data: [150, 212, 201, 154, 190, 330, 410]
          },
          {
            name: 'O3',
            type: 'bar',
            stack: 'total',
            color:'#63b2ee',
            label: {
